<template>
  <div class="menu">
    <center>
      <button v-on:click ="showMenu();">사용자</button>
      <button v-on:click ="showMenuAdmin();">관리자</button>
    </center>
    <div v-if="menu">
      <Menu />
    </div>
    <div v-if="menu_admin">
      <MenuAdmin />
    </div>
    <!-- <DigitalClock /> -->
  </div>
</template>

<script>

import Menu from './Menu';
import MenuAdmin from './MenuAdmin';
import DigitalClock from './DigitalClock.vue';

export default {
  name: 'Left',
  data() {
    return {
            // 사용자 우선
            // menu: true, 
            // menu_admin: false,
            
            // 관리자 우선
            menu: false, 
            menu_admin: true,
    }
  },
  components: {
    Menu: Menu,
    MenuAdmin: MenuAdmin,
    DigitalClock, 
  },
  methods: {
    showMenu(){
      // alert('showMenu');
      this.menu=true;
      this.menu_admin=false;
    }, 
    showMenuAdmin(){
      // alert('showMenuAdmin');
      this.menu=false;
      this.menu_admin=true;
    }
  }
}
</script>
