<template>
    <div>
        <h6>검색 기능 개발 중</h6>
        <div>
        <!-- <button v-for="item in selectList" v-bind:key="item.id" v-on:click="selectData(item.router)">{{item.names}}</button> -->
        </div>
    </div>
    <!-- Search Componenet -->
    <button v-for="item in searchList" v-bind:key="item.id" v-on:click="setData(item.contentsId, item.router)">{{item.names}}</button>
    <br>
    <!-- <input type="text" v-model="paramInput" placeholder="검색어" /><button v-on:click="searchData(paramInput)">검색</button> -->
    <input type="text" v-model="pInput" /><button v-on:click="searchData(pInput)">검색</button>
    <div class="row">
        <table>
        <thead> 
            <tr>
            <th v-for="(a,i) in resultKey" :key="i">{{ this.resultKey[i] }}</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(row,rid) in resultArr" :key="rid">
            <td v-for="(col,cid) in row" :key="cid" v-text="col"></td>
            </tr>
        </tbody>
        </table>
    </div>
</template>

<script>
var resultJson;
var pInput;
var resultArr; 

export default {
    components: {
        },
    data() {
            return {
                // selectList: [
                //     {names: '검색합시다.', router: '/oridata', id: 1},
                // ],
                searchList: [
                    {names: '표준단어검색', router: '/da_std_word_search', id: 1, contentsId: 7},
                    {names: '표준용어검색', router: '/da_std_word_search', id: 2, contentsId: 8},
                ],
                resultJson,
                resultArr: [],
                resultKey: [],
                
                pInput,
                resData: [],
            }
        },
    mounted() { 
    },  
    methods: {
        setData(contentsId, dataNm){
            this.dataNm = dataNm;
            this.contentsId = contentsId;
        },
        searchData(pInput){
            const axios = require('axios');
            // this.$axios.get(this.dataNm+'?searchWord='+pInput)
            axios.post(this.dataNm, {contentsId: this.contentsId, searchWord: pInput})
                    .then(response => {
                        this.resultJson = response.data;
                        this.resultArr = this.resultJson[0].get_result_json;
                        this.resultKey = Object.keys(this.resultArr[0]);
                    })
                    .catch(error => {
                        console.log(error)
                    })
        },
    },  
}



</script>

<style>
/* @import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap"); */
</style>