<template>
  <div>
    <h6>[Menu7-5.vue]</h6>
    <div>
      <button v-for="item in selectList" v-bind:key="item.id" v-on:click="selectData(item.router)">{{item.names}}</button>
    </div>
  </div>
  <div class="row">
      <table class=".table-sm">
      <thead> 
          <tr>
          <th v-for="(a,i) in resultKey" :key="i">{{ this.resultKey[i] }}</th>
          </tr>
      </thead>
      <tbody>
          <tr v-for="(row,rid) in resultArr" :key="rid">
          <td v-for="(col,cid) in row" :key="cid" v-text="col"></td>
          </tr>
      </tbody>
      </table>
  </div>
</template>

<script>
  import Menu7_Nav from './Menu7_Nav';
  var resultJson;
  
  export default {
    components: {
              Menu7_Nav: Menu7_Nav
          },
    data() {
              return {
                selectList: [
                    {names: '식품영양성분(원본-샘플)', router: '/oridata', id: 1},
                    {names: '나의반려식물', router: '/oridata', id: 1},
                  ],
                resultJson,
                resultArr: [],
                resultKey: [],
            }
          },
    mounted() { 
                  
        },  
    methods: {
          selectData(dataNm){
            const axios = require('axios');
            this.$axios.get(dataNm)
                .then(response => {
                    this.resultJson = response.data;
                    this.resultArr = this.resultJson[0].get_result_json;
                    this.resultKey = Object.keys(this.resultArr[0]);
                })
                .catch(error => {
                    console.log(error)
                })
          }
        },  
  }
</script>

